import Particle from './Particle'

function Contact() {

  return (
    <div className="contact-wrapper" id="contact">
        
        <div className="message">
            <form className="form">
                <input className="name input" placeholder="Name" />
                <input className="email input" placeholder="Email" />
                <textarea className="msg input" placeholder="Your Message"></textarea>
                <input type="submit" className="submit" />
            </form>
        </div>

        <div className="wrapper">
            
            <div className="contact-title" style={{color: "#fff"}}>
                <div>CONTACT ME</div>
                <a href="https://www.linkedin.com/in/ankur-yadav-738083164/"><i className="fab fa-linkedin"></i></a>
                <a href="https://github.com/ankur221b"><i className="fab fa-github"></i></a>
            </div>
            
           
        </div>

    </div>
  );
}
export default Contact;
